// В этом файле должны быть стили для БЭМ-блока page-social, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page-social {

  $block-name:                &; // #{$block-name}__element

  // font-family: Barlow-SemiBold;
  background-image: url('../img/bg-page-social.png');
  background-position: center;
  background-size: cover;

  &__inner {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-areas: 
    'navigation item1 .'
    'navigation item1 item3'
    'navigation item2 item3'
    'navigation item2 item4'
    'navigation . item4';
  }
  &__nav {
    grid-area: navigation;
  }
  &__item1 {
    grid-area: item1;
  }
  &__item2 {
    grid-area: item2;
  }
  &__item3 {
    grid-area: item3;
  }
  &__item4 {
    grid-area: item4;
  }
}
