// В этом файле должны быть стили для БЭМ-блока album, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority
@font-face {
  font-family: 'Barlow-SemiBold';
  src: url('../fonts/Barlow-SemiBold.otf')
}
@font-face {
  font-family: 'Roboto';
  src: url('../fonts/Roboto.ttf')
}
.album {

  $block-name:                &; // #{$block-name}__element

  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__titleName {
    font-family: Barlow-SemiBold;
    font-size: 60px;
    text-align: center;
    color: #fff;
  }
  &__rectangle {
    margin-bottom: 35px;
  }
  &__subtitle {
    font-family: Roboto;
    font-size: 20px;
    text-align: center;
    color: #778fc3;
    margin-bottom: 95px;
  }
  &__column_1 {
    position: relative;
    display: flex;
    align-items: center;
  }
  &__column_2 {
    margin-left: 30px;
    margin-right: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__column_3 {
    display: flex;
    align-items: center;
    position: relative;
  }
  &__albums {
    display: flex;
    padding-bottom: 300px;
  }
  &__logo {
    margin-top: 80px;
  }
  &__rectangle1 {
    position: absolute;
    bottom: 248px;
    right: 31px;
    width: 182px;
    height: 182px;
    border-radius: 2px;
    background-image: linear-gradient(to top, #fed41c, #6aff7f);
  }
  &__rectangle2 {
    position: absolute;
    right: 105px;
    bottom: 95px;
    width: 32px;
    height: 32px;
    border-radius: 2px;
    background-image: linear-gradient(to top, #26feff, #072293);
  }
  &__rectangle3 {
    position: absolute;
    top: 143px;
    left: 30px;
    width: 132px;
    height: 132px;
    border-radius: 2px;
    background-image: linear-gradient(to top, #26feff, #072293);
  }
  &__rectangle4 {
    position: absolute;
    top: 41px;
    right: 77px;
    width: 31px;
    height: 31px;
    border-radius: 2px;
    background-image: linear-gradient(115deg, #fd5960, #101783);
  }
  &__circle1 {
    position: absolute;
    bottom: 70px;
    right: 130px;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    background-color: #6aff7f;
  }
  &__circle2 {
    position: absolute;
    bottom: -45px;
    right: 15px;
    width: 96px;
    height: 96px;
    border-radius: 50%;
    background-color: #33cbcc;
  }
}
